::-webkit-scrollbar-track {
    border-radius: 10px;
    background: transparent !important;
  }
  ::-webkit-scrollbar {
    width: 8px;
    /* background-color: #f1f1f1; */
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #2d2d2d;
    transition: opacity 0.2s ease-in-out;
  }
  *::-moz-selection {
    background: #2d2d2d;
    transition: opacity 0.2s ease-in-out;
    color: #f1f1f1;
    text-shadow: none;
  }
  ::-moz-selection {
    background: #2d2d2d;
    transition: opacity 0.2s ease-in-out;
    color: #f1f1f1;
    text-shadow: none;
  }
  ::selection {
    /* background: #2d2d2d; */
    /* transition: opacity 0.2s ease-in-out; */
    color: #f1f1f1;
    text-shadow: none;
  }

  .magic-cursor {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: red; /* You can customize the color */
    border-radius: 50%;
    pointer-events: none;
    transform: translate(-50%, -50%);
    transition: width 0.3s, height 0.3s;
}
